.figure {
  z-index: 2;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  transition: opacity 200ms ease-in;
  display: flex;
  justify-content: center;
  align-items: center;
  opacity: 0;

  &.paused {
    opacity: 1;
  }
}

.logo {
  width: 80%;
  max-width: 1600px;
}
